import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Registro_treinamento } from '../../../modelo/Registro_treinamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'registro_treinamento-dados-details',   templateUrl: 'registro_treinamento_dadosDetails.component.html' }) export class Registro_treinamentoDadosDetailsComponent implements OnInit {    registro_treinamento: Registro_treinamento;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.registro_treinamento.Registro_treinamento";                 let id = +params['id'];         this.registro_treinamento = new Registro_treinamento();         this.registro_treinamento.cd_registro_treinamento = id;         o.objetoJson = this.registro_treinamento;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a registro_treinamento. '; }      this.registro_treinamento = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


